.button {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #cd4d31;
  border: 1px solid transparent;
  width: 100%;
  height: 60px;
  text-decoration: none;

  cursor: pointer;

  transition: 0.3s;

  &:hover {
    background: #af3419;
  }

  &:active {
    background: #ea340d;
  }

  @include onTablets {
    width: 330px;
    height: 60px;
  }

  @include onDesctop {
    width: 270px;
    height: 60px;
  }

  &__text {
    display: block;
    text-decoration: none;
    font-family: "Playfair Display", serif;
    font-size: 16px;
    line-height: 100%;
    font-weight: 600;
    color: #f1f5f4;
  }
}
